<template>
    <div class="V_mxTest">
        <el-row :gutter="20">
            <!--  BEGIN -->
            <el-col :span="12">
                <el-button type="primary" class="fl mb20" @click="fn_addProject"></el-button>
                <el-table  :data="dt_projectList"  border  style="width: 100%">
                    <el-table-column    label=""  >
                        <template  slot-scope="scope">
                                <el-autocomplete
                                v-model="scope.row.projectName"
                                class="inline-input medium-width medium-input"
                                placeholder=""
                                :trigger-on-focus="false"
                                :fetch-suggestions="fn_querySearch"
                                @select="((item)=>fn_selectProject(item,scope))"
                                ></el-autocomplete>
                        </template>
                    </el-table-column>
                    <el-table-column    label="" >
                        <template slot-scope="scope">
                             <el-select v-model="scope.row.chapterId" @change="fn_selectChapter(scope.row)" class="medium-input" size="medium" placeholder="">
                                <el-option
                                v-for="item in scope.row.chapterList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </template>
                    </el-table-column>

                    <el-table-column   width="140" label="" align="center">
                            <template slot-scope="scope">
                                <el-tag type="info" v-if="scope.row.xhrState == 'no'" v-text="dt_xhrState['no']"></el-tag>
                                <el-tag  v-if="scope.row.xhrState == 'load'" v-text="dt_xhrState['load']"></el-tag>
                                <el-tag type="success" v-if="scope.row.xhrState == 'success'" v-text="dt_xhrState['success']"></el-tag>
                                <el-tag type="danger" v-if="scope.row.xhrState == 'failure' || scope.row.xhrState == 'contentNull'" v-text="dt_xhrState[scope.row.xhrState]"></el-tag>
                            </template>
                    </el-table-column>
                    <el-table-column  width="120" label="">
                        <template slot-scope="scope">
                           <el-button
                            size="mini"
                            type="danger"
                            @click="fn_deleteRow(scope.$index)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
            <!--  END -->
            

            <!--  BEGIN -->
            <el-col :span="6" class="mxTest-right fr">
                 <div class="mxTest-right-title mb20 ">
                     <el-button type="primary" size="medium" @click="fn_batchSaveChaper"></el-button>
                 </div>

                 <el-form label-width="100px" class="form-newClass">
                      <el-form-item  label="">
                            <el-select  v-model="dt_bubbleType"  placeholder="">
                                <el-option
                                v-for="coreBubble in dt_coreBubble"
                                :key="coreBubble.value"
                                :label="coreBubble.label"
                                :value="coreBubble.value"
                                :disabled="coreBubble.disabled">
                                </el-option>
                            </el-select>
                      </el-form-item>
                      <el-form-item label="">
                           <el-input type="textarea" v-model="dt_bubbleContent"></el-input>
                      </el-form-item>
                 </el-form>
            </el-col>
            <!--   END -->
           
        </el-row>



    </div>
</template>

<script>
  //
 import {axiosQueryProject,axiosSaveStructures,axiosGetStructures}  from '../../service/requestConfig.js'
 import ut from '../../utils/utils.js'
 import {mapGetters} from 'vuex'
  export default {
    created() {
        this.init();
    },
    data() {
      return {
        //
        dt_projectList: [],
        //
        dt_projectItem:{
            //
            projectName:"",
            //id
            projectId:"",
            //id
            chapterId:"",
            //
            chapterList:[],
            //
            chapterContent:"",
            //
            xhrState:'no'
        },
        //
        dt_coreBubble:[{
                value:111,
                label:"",
            },{
                value:110,
                label:"CG",
            },{
                value:112,
                label:"",
            },{
                value:100,
                label:"",
                disabled:true
            },
        ],
        //
        dt_bubbleType:111,
        //
        dt_bubbleContent:"",
        //
        dt_xhrState:{
            no:"",
            load:"....",
            success:"",
            failure:"",
            contentNull:"",
        },
        //loading
        dt_loading:null
      }
    },
    computed:{
        ...mapGetters([
            'getApiVersion',
        ])
    },
    methods: {
        //
        init(){
            this.initFn();
        },
        //
        initData(){

        },
        //
        initFn(){
            this.fn_defultProjectData();
        },
        //
        fn_defultProjectData(){
            if(this.getApiVersion != 'test')return;
            //
            let _initProjectList = [
                {
                     projectId:"d5ab4e08f34a4a97ae8a0eac4b41b205",
                     projectName:"",
                     default:true
                },{
                     projectId:"2a00abf79869447dab04fcffd418a550",
                     projectName:"2",
                     default:true
                },{
                     projectId:"164462301ee242d9896281a5f32e7a1e",
                     projectName:"3",
                     default:true
                },{
                     projectId:"64b5349b785949b2a620625ef6200558",
                     projectName:"4",
                     default:true
                },{
                     projectId:"402810e0833746899a86f3734b922b04",
                     projectName:"5",
                     default:true
                },
                {
                     projectId:"07180cad46ea4a6a8958cb9cf649e024",
                     projectName:"6",
                     default:true
                },
                {
                     projectId:"2d91c15d1c6742fb9d28002670609097",
                     projectName:"7",
                     default:true
                },{
                     projectId:"b495234220064d3787e956e225542ae1",
                     projectName:"8",
                     default:true
                }
                ,{
                     projectId:"2bd16d228cba4f68992f49fd969e2960",
                     projectName:"9",
                     default:true
                }
            ]
            let _projectId = {}
            let _projectRow = {}
            //
            _initProjectList.forEach(item=>{
                let _projectItem = JSON.parse(JSON.stringify(this.dt_projectItem));
                _projectItem = Object.assign(_projectItem,item);
                this.dt_projectList.push(_projectItem)
                _projectId = {projectId:item.projectId}
                _projectRow = {row:_projectItem}
                this.fn_selectProject(_projectId,_projectRow);
            })

        },
        //
        fn_addProject(){
            this.dt_projectList.push(this.dt_projectItem)
        },
        /**     
         * 
         * @param {String} index=>
        */
        fn_deleteRow(index){
            this.dt_projectList.splice(index,1);
        },
        /**
         * 
         * @param {String}  queryValue:  
         * @param {String}          cb:  
         */
        fn_querySearch(queryValue, cb){
           if(!queryValue || queryValue == "")return;
           axiosQueryProject(queryValue).then(resp=>{
                let _serverData  = resp.data;
                let _arr = [];
                if(_serverData.isOk && _serverData.data && _serverData.data.length){
                    _arr = _serverData.data.map((item)=>{
                        return {
                            value:item.work_name+'  ['+item.username+']',
                            projectId:item.project_id
                        }
                    })
                }
                cb(_arr);
            })
        },
        /**
         * 
         * @param  {Object}        item=> id
         * @param  {Object}  currentRow=> dt_projectList element ui
         */
        fn_selectProject(item,currentRow){
            currentRow.row.projectId = item.projectId;
            //
            this.xhr_getProjectFile(item.projectId,['chapterinfo.bin'],(data)=>{
                //
                currentRow.row.chapterName ="";
                currentRow.row.chapterList = [];
                //
                data.chapter_list.forEach((chapter,index)=>{
                   
                    currentRow.row.chapterList.push({
                        value:chapter.chapter_id,
                        label:chapter.chapter_name
                    })
                    index == 0 ? currentRow.row.chapterId = chapter.chapter_id  : '';
                    if(index == 0){
                        this.fn_defaultSelectChapter(currentRow.row);
                    }                    
                })
    
            });
        },
        /**
         * 
         * @param {String}          projectId=>id
         * @param {Array}      structuresName=>
         * @param {Function}               cb=>  
         */
        xhr_getProjectFile(projectId,structuresName,cb){
            axiosGetStructures(projectId,JSON.stringify(structuresName)).then(resp=>{
                //
                let _handlerData = {};
                if(structuresName.length > 1){
                    resp.data.data.map(item=>{
                          let _name  =  item.structureName.indexOf("chapter_") != -1 ? 'chapterContent' :  item.structureName.split('.')[0];
                           _handlerData[_name] = this.ut_handlerChapterData(item);
                     })
                }else{
                    _handlerData = this.ut_handlerChapterData(resp.data.data[0]);
                }
               
                cb(_handlerData);
            },()=>{
                 this.fn_closeLoading();
            }).catch((err)=>{
                 this.fn_closeLoading();
                console.log("",err)
                this.ut_showMessage("error",'');
            })
        },
        /**  
         * 
         * @param {Object}  data=>chapterinfo
        */
        ut_handlerChapterData(data){
            if(Object.prototype.toString.call(data) != "[object Object]")return null;
            let _newData = null;
            if(data.materialVer == 'v3'){
                _newData =  JSON.parse(data.structureContent_v3)
            }else{
                _newData = JSON.parse(data.structureContent)
            }
            if(data.structureName.indexOf("chapter") == -1){
                _newData = JSON.stringify(_newData);
            }
            return _newData;
        },
        /**  
         * 
         * @param {Object} row=>
        */
        fn_selectChapter(row){
            //loading
            this.fn_showLoading();
            let _structuresName = ['rolelist.bin','filemap.bin','chapterinfo.bin',`chapter_${row.chapterId}.bin`]
            this.xhr_getProjectFile(row.projectId,_structuresName,(item)=>{
                row = Object.assign(row,item);
                console.log("row",row);
                //loading
                this.fn_closeLoading();
            })
        },
        fn_defaultSelectChapter(row){
            //loading
            this.fn_showLoading();
            let _structuresName = ['rolelist.bin','filemap.bin','chapterinfo.bin',`chapter_${row.chapterId}.bin`]
            this.xhr_getProjectFile(row.projectId,_structuresName,(item)=>{
                row = Object.assign(row,item);
               if(this.dt_projectList.indexOf(row) >= (this.dt_projectList.length-1) ){
                    //loading
                    this.fn_closeLoading();
               }
            })
        },
        //
        fn_batchSaveChaper(){
            if(!this.dt_projectList.length){
                this.ut_showMessage('error','');
                return;
            }
            //
            this.dt_projectList.forEach(item=>{
                this.xhr_saveChapter(item);
            })
        },
        /** 
         * 
         * @param {String} projectId=>id
         * @param {String} chapterId=>id
        */
        xhr_saveChapter(item){
           
            //
            if(!item.chapterContent){
                    item.xhrState = 'contentNull';
                    return;
            }else{
                 //
                 item.xhrState = 'load';
                 item.chapterContent = this.ut_addChapterData(item.chapterContent);
            }
           
            let _structuresName = ['rolelist.bin','filemap.bin','chapterinfo.bin',`chapter_${item.chapterId}.bin`]
            let _structuresContent = [JSON.parse(item.rolelist),JSON.parse(item.filemap),item.chapterinfo,item.chapterContent]
            //
            axiosSaveStructures(item.projectId,_structuresName,_structuresContent).then(resp=>{
                    let _data = resp.data;
                    if(_data.isOk){
                        item.xhrState = 'success';
                    }else{
                        item.xhrState = 'failure';
                    }
            },()=>{
                item.xhrState = 'failure';
            }).catch(err=>{
                item.xhrState = 'failure';
                console.log("",err);
            })
        },
        /** 
         * 
         * 
        */
        ut_addChapterData(data){
            if(!data)return;
            data.cmd_sequence_list.push(this.ut_createBubbleData());
            return data;
        },
        // 
        ut_createBubbleData(){
            let _bubble = null;
            switch(this.dt_bubbleType){
                //
                case 100:
                    _bubble = {
                        cmd_event: {
                            argv: {
                               image_bean_id: "260ce7531b2f36a9",
                                image_id: "roleImg_10488901",
                                mood_id: 0,
                                pic_express_id: "",
                                role_id: "e73d3b9b29593bca",
                                text: this.dt_bubbleContent,
                            },
                            cmd_key: "show_text",
                            code: 100
                        },
                        cmd_list:[],
                        i_id: ut.createId(),
                        s_id: ut.createId()
                    }
                    break;
                //
                case 111:
                     _bubble = {
                        cmd_event: {
                            argv: {
                                text: this.dt_bubbleContent,
                            },
                            cmd_key: "show_text_middle",
                            code: 111
                        },
                        cmd_list:[],
                        i_id: ut.createId(),
                        s_id: ut.createId()
                    }
                    break;
                //CG
                case 110:
                    _bubble = {
                        cmd_event: {
                            argv: {
                                text: this.dt_bubbleContent,
                                cg_id: "",
                                text_pos: 0
                            },
                            cmd_key: "show_cg",
                            code: 110
                        },
                        cmd_list:[],
                        i_id: ut.createId(),
                        s_id: ut.createId()
                    }
                    break;
                //
                case 112:
                    _bubble = {
                    cmd_event: {
                        argv: {
                            text: this.dt_bubbleContent,
                            text_pos: 0
                        },
                        cmd_key: "show_aside",
                        code: 112
                    },
                    cmd_list:[],
                    i_id: ut.createId(),
                    s_id: ut.createId()
                }
                break;
            }   
            return _bubble;
        },
        //loading
        fn_showLoading(){
            this.fn_closeLoading();
            this.dt_loading = this.$loading({
                lock: true,
                text: 'Loading',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });
        },
        //loading
        fn_closeLoading(){
            this.dt_loading && this.dt_loading.close();
        }
    }
  }
</script>

<style scoped>
    @import './mxTest.css';
</style>
 