import Vue from 'vue'
import { Message } from 'element-ui';
const ConfigMessgae = {
    //
    dataRenewSuccess:"",
    //
    dataRenewFailure:"",
    //
    searchNull:"",
    //
    projectPublishNull:"",
    //
    serviceUnusual:"",
    //
    userPwdInexistence:"",
    //
    loginSuccess:""
}

let messgaeItem = {
    //
    message(message){
        Message(message);
    },
    //
    success(message){
        Message({
            message: message,
            type: 'success'
        });
    },
    //
    warning(message){
        Message({
            message: message,
            type: 'warning'
        });
    },
    // 
    error(message){
        Message.error(message);
    }
}


const ut_showMessage = function(type,message){
    let _message = ConfigMessgae[message] ? ConfigMessgae[message] : message;
    if(messgaeItem[type]){
        messgaeItem[type](_message);
    }else{
        messgaeItem.warning('  ');
    }
}


export default ut_showMessage;