import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);


let  state = {
    //
    isLogin:false,
    //token
    mxToken:null,
    //
    projectNumber:0,
    //id
    projectId:0,
    //
    projectName:"",
    //
    apiVersion:'test',
    //
    st_nextRouter:'/'
}
//
let checkLogin = ()=>{
    //token 
    let _getToke = window.localStorage.getItem("token");
    if(_getToke){
        state.mxToken = _getToke;
        return true
    };
    return false;
}
//
let checkVersion = ()=>{
    //token 
    let _version = window.localStorage.getItem("mx_version");
    if(_version){
        state.apiVersion = _version;
        return true
    };
    return false;
}
checkVersion();
if(checkLogin()){
    state.isLogin = true;
}





const Store = new Vuex.Store({
    state,
    getters:{
        //
        getLoginState(state){
            return state.isLogin;
        },
        //token
        getToken(state){
            return state.mxToken;
        },
        //
        getProjectNumber(state){
            return state.projectNumber;
        },
        //id
        getProjectId(state){
            return state.projectId;
        },
        //
        getProjectName(state){
            return state.projectName;
        },
        //
        getApiVersion(state){
            return state.apiVersion;
        },
        //
        getStNextRouter(state){
            return state.st_nextRouter;
        }
    },
    //
    mutations:{
        //token
        setLoginState(state,param){
            state.isLogin = param;
            state.mxToken = param;
            window.localStorage.setItem('token',param ? param : '');
        },
        //
        setProjectNumber(state,num){
             state.projectNumber = num;
        },
        //id
        setProjectId(state,id){
            state.projectId = id;
        },
        //
        setProjectName(state,name){
            state.projectName = name;
        },
         //
         setApiVersion(state,version){
            state.apiVersion = version;
            window.localStorage.setItem('mx_version',version);
        },
        //
        setStNextRouter(state,router){
            state.st_nextRouter = router;
            window.localStorage.setItem('mx_nextRouter',router);
        }
    }
})














export default Store;